.comment {
  position: relative;
  background: #fff;
  padding: 0 24px 18px;
  @include response(1200px) {
    padding: 0;
  }
  &__btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #f0f2f2;
    padding: 12px 24px;
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
    color: #4d6562;
    border-radius: 2px;
    cursor: pointer;
    transition: .3s ease-in-out;
    @include desktop-hover() {
      background: #dfe3e3;
    }
  }
  &__box {
    position: relative;
    padding: 24px 24px 13px;
    background: #f0f2f2;
    border-radius: 2px;
    @include response(1200px) {
      padding: 16px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &__text {
    position: relative;
    margin-bottom: 16px;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    color: #01231f;
    @include response(1200px) {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      padding-right: 10px;
      margin-bottom: 0;
    }
    p {
      margin: 0;
      display: inline;
    }
  }
  &__edit {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 2px;
    border: 2px solid #80918f;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    padding: 0 4px;
    color: #80918f;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      flex: 0 0 auto;
    }
    @include desktop-hover() {
      background: #dcdcdc;
    }
    svg {
      flex: 0 0 auto;
      margin-right: 3px;
      fill: #80918f;
    }
  }
}
