$row-back: #f3f6f9;
$primary-color: #ff3d00;
$button-background: #80918f;
$text-color: #01231f;
$secondary-color: #0d9a88;
$disabled-color: #dcdcdc;
$cart-color: #4d6562;
$alert-background: #f6f6f6;

body {
  overflow: visible;
  @media screen and (max-width: 992px) {
    &.hide-overflow {
      overflow: hidden;
    }
    &.scrolling-up {
      .custom-made-cart-wrapper {
        .content-area {
          .expanded-rows {
            height: 630px;
          }
          .single-item-row {
            .row-content {
              max-height: 500px;
            }
          }
        }
      }
    }
  }
}

@-webkit-keyframes slide {
  0% {
    opacity: 0;
    transform: translateX(-15px);
  }
  20% {
    opacity: 1;
    transform: translateX(-9px);
  }
  80% {
    opacity: 1;
    transform: translateX(9px);
  }
  100% {
    opacity: 0;
    transform: translateX(10px);
  }
}
@keyframes slide {
  0% {
    opacity: 0;
    transform: translateX(-15px);
  }
  20% {
    opacity: 1;
    transform: translateX(-9px);
  }
  80% {
    opacity: 1;
    transform: translateX(9px);
  }
  100% {
    opacity: 0;
    transform: translateX(10px);
  }
}

body {
  overflow: visible;
  @media screen and (max-width: 992px) {
    &.hide-overflow {
      overflow: hidden;
    }
    &.scrolling-up {
      .custom-made-cart-wrapper {
        .content-area {
          .expanded-rows {
            height: 370px;
          }
          .single-item-row {
            .row-content {
              max-height: 240px;
            }
          }
        }
        .cart-content-wrapper {
          .popup-on-mobile {
            height: calc(100dvh - 100px);
          }
        }
      }
    }
  }
}

.com-btn {
  background-color: $primary-color;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  color: #fff;
  border-radius: 1px;
  text-decoration: none;
  font-weight: 500;
  img {
    padding-left: 10px;
  }
  &:hover {
    background-color: #fa5a28;
  }
}

.popup-sword-part,
.popup-confirmation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: rgba(0, 0, 0, 0.4);
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  &.active {
    opacity: 1;
    visibility: visible;
    transition: all 0.3s ease-in-out;
  }
  .middle-content,
  .middle-col {
    width: 90%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    padding: 20px;
    max-height: 80vh;
    overflow-y: auto;
    .top-bar-wrapper {
      display: flex;
      align-items: flex-start;
      gap: 15px;
      ul {
        list-style: none;
        display: flex;
        align-items: center;
        gap: 10px;
        margin: 0;
        padding: 0;
        flex-direction: row;
        flex-wrap: wrap;
        flex-grow: 1;
        li {
          flex-basis: calc(50% - 10px);
          text-align: center;
          border: 1px solid $disabled-color;
          text-transform: uppercase;
          font-weight: 600;
          padding: 8px 5px;
          transition: all 0.3s ease-in-out;
          color: $button-background;
          &.active,
          &:hover {
            transition: all 0.3s ease-in-out;
            background-color: $secondary-color;
            border-color: $secondary-color;
            color: #fff;
            cursor: pointer;
          }
          span {
            display: block;
            font-size: 12px;
            line-height: 14px;
          }
        }
      }
      button.close {
        border: none;
        padding: 5px;
        background: $button-background;
        width: 35px;
        height: 30px;
        cursor: pointer;
      }
    }

    .img-wrapper {
      margin-top: 20px;
      img {
        width: 100%;
      }
    }
  }
}
.popup-confirmation-overlay,
.popup-sword-part-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.popup-confirmation {
  .header-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
    padding-bottom: 20px;
    margin-bottom: 20px;
    h3 {
      font-size: 20px;
      margin: 0;
      line-height: 20px;
      font-weight: 500;
    }
    button {
      width: 26px;
      height: 26px;
      background-color: #b4bebd;
      border-radius: 3px;
      border: none;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      transition: all 0.3s ease-in-out;
      &:hover {
        transition: all 0.3s ease-in-out;
        background-color: $button-background;
      }
      svg {
        fill: #fff;
        width: 90%;
        height: 90%;
      }
    }
  }
  .content {
    p {
      font-weight: 100;
      line-height: 24px;
      font-size: 16px;
    }
    .button-wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;
      a {
        display: block;
        font-size: 14px;
        text-transform: uppercase;
        border-radius: 3px;
        color: #fff;
        background-color: #80918f;
        text-decoration: none;
        padding: 8px 20px;
        min-width: 100px;
        text-align: center;
        transition: all 0.3s ease-in-out;
        &:hover {
          transition: all 0.3s ease-in-out;
          background-color: #63716f;
        }
        &.color {
          background-color: $primary-color;
          &:hover {
            transition: all 0.3s ease-in-out;
            background-color: #fa5a28;
          }
        }
      }
    }
  }
}

.selec-input {
  font-weight: 100;
  font-size: 14px;
  display: flex;
  align-items: center;
  input {
    display: none;
    line-height: 15px;
    &:checked + label:before {
      content: "\2713";
      background-color: $primary-color; /* Change the checkmark background color when checked */
      border: 1px solid $primary-color;
    }
  }
  label {
    line-height: 15px;
    padding-left: 5px;
    &::before {
      position: relative;
      left: -5px;
      top: -2px;
      content: "";
      display: inline-block;
      width: 15px;
      height: 15px;
      text-align: center;
      line-height: 15px;
      background-color: transparent;
      color: #fff; /* Change the checkmark color */
      border: 1px solid $disabled-color;
      border-radius: 3px;
      vertical-align: middle;
    }
  }
  span {
    display: none;
  }
}

.custom-made-cart-wrapper {
  color: #01231f;
  .cart-main-wrapper {
    .view-all-button {
      display: none;
    }
  }

  .cart-alert-wrapper {
    border: 1px solid rgba(0, 0, 0, 0.2);
    background-color: $alert-background;
    display: flex;
    align-items: flex-start;
    gap: 10px;
    border-radius: 3px;
    margin-top: 20px;
    padding: 15px;
    .icon {
      width: 170px;
      img {
        width: 100%;
      }
    }
    a {
      text-decoration: none;
      color: $primary-color;
      font-weight: 600;
    }
    p {
      margin: 0;
      font-weight: 400;
      line-height: 25px;
      padding-left: 15px;
      font-size: 17px;
      border-left: 2px solid $text-color;
    }
  }

  .view-all-button {
    display: flex;
    align-items: center;
    gap: 5px;
    justify-content: center;
    background: $button-background;
    padding: 14px 24px;
    text-decoration: none;
    transition: all 0.3s ease-in-out;
    &:hover {
      background-color: #4d6562;
    }
    .icon {
      width: 15px;
      height: 15px;
      img {
        width: 100%;
        display: block;
      }
      svg {
        width: 15px;
        height: 15px;
        fill: #fff;
      }
    }
    span {
      color: #fff;
      font-weight: 700;
    }
  }
  .header-wrapper {
    text-align: center;
    padding: 16px;
    margin-bottom: 16px;
    > h1 {
      @include response(1200px) {
        font-size: 26px;
      }
    }
    h1,
    p {
      color: $text-color;
    }
    h2 {
      font-weight: 600;
      font-size: 30px;
    }
    p {
      font-weight: 100;
    }
    a {
      margin-top: 8px;
      max-width: 320px;
      margin: 0 auto;
    }
  }

  &.steps-screens {
    padding-top: 44px;
    .header-wrapper {
      padding: 0px;
      margin-bottom: 35px;
      a {
        max-width: 100%;
        background: #f0f2f2;
        svg {
          fill: #80918f;
        }
        span {
          color: #4d6562;
          font-weight: 500;
        }
      }
      .mobile-popup {
        position: fixed;
        top: 160px;
        right: 15px;
        width: 50px;
        height: 50px;
        z-index: 30;
        border-radius: 100%;
        border: 2px solid #fff;
        background: $button-background;
        justify-content: center;
        align-items: center;
        padding: 0;
        display: flex;
        box-shadow: 6px 5px 15px rgba(0, 0, 0, 0.2);
        transition: all 0.3s ease-in-out;
        transform: scale(0);
        cursor: pointer;
        @include response(1200px) {
          top: 110px;
          transform: none;
        }
        &.active {
          transition: all 0.3s ease-in-out;
          transform: scale(1);
        }
        .icon {
          width: 25px;
          height: 25px;
          display: block;
          svg {
            fill: #fff;
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }

  .cart-content-wrapper {
    &.active {
      .popup-on-mobile {
        top: 100px;
        opacity: 1;
        visibility: visible;
        transition: all 0.3s ease-in-out;
      }
      &::after {
        position: fixed;
        content: "";
        top: 0;
        height: 100%;
        width: 100%;
        left: 0;
        background-color: rgba(0, 0, 0, 0.4);
        z-index: 9;
        opacity: 0;
        transition: all 0.3s ease-in-out;
        visibility: visible;
      }
    }
    &::after {
      opacity: 0;
      transition: all 0.3s ease-in-out;
      visibility: hidden;
    }
    .popup-on-mobile {
      position: fixed;
      top: 200px;
      width: 100%;
      left: 0;
      z-index: 1000;
      padding: 12px;
      background-color: #fff;
      z-index: 40;
      display: flex;
      align-items: flex-end;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease-in-out;
      box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.12);
      height: calc(100dvh - 100px);
      .card-main-wrapper {
        max-height: 600px;
        overflow-y: auto;
      }
    }

    .lenght-selection {
      max-width: 500px;
      margin-bottom: 30px;
      position: relative;
      .select-title {
        font-size: 12px;
        color: $button-background;
        text-transform: uppercase;
        display: block;
        margin-bottom: 5px;
        font-weight: 500;
      }
      .wrap-selection {
        border: 1px solid $button-background;
        border-radius: 3px;
        padding: 15px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        transition: all 0.3s ease-in-out;
        &:hover {
          transition: all 0.3s ease-in-out;
          cursor: pointer;
          background-color: $disabled-color;
        }
        span {
          pointer-events: none;
          color: $text-color;
          span {
            background: $secondary-color;
            color: #fff;
            text-transform: uppercase;
            font-size: 12px;
            padding: 2px 5px;
            border-radius: 3px;
            font-weight: 500;
            margin-left: 5px;
          }
          b {
            color: $primary-color;
          }
        }
        .icon {
          &::after {
            transition: all 0.15s ease-in-out;
            content: "";
            display: block;
            height: 0;
            width: 0;
            border-top: 5px solid #01231f;
            border-left: 4.16667px solid transparent;
            border-right: 4.16667px solid transparent;
          }
        }
      }

      .dropdown {
        position: absolute;
        top: calc(100% + 14px);
        width: 100%;
        z-index: 5;
        background-color: #fff;
        border-bottom-left-radius: 3px;
        border-bottom-right-radius: 3px;
        box-shadow: 4px 13px 11px 9px rgba(0, 0, 0, 0.2);
        opacity: 0;
        visibility: hidden;
        transition: all 0.3s ease-in-out;
        &.active {
          opacity: 1;
          visibility: visible;
          top: calc(100% + 1px);
          transition: all 0.3s ease-in-out;
        }
        ul {
          list-style: none;
          padding: 0;
          margin: 0;
        }
        .wrap-row {
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 15px;
          transition: all 0.3s ease-in-out;
          border-bottom: 1px solid $disabled-color;
          &:hover {
            transition: all 0.3s ease-in-out;
            background-color: $disabled-color;
            cursor: pointer;
          }
          &.highlighted {
            .value {
              font-weight: 500;
            }
          }
          .value {
            display: inline-block;
            vertical-align: middle;
            font-weight: 100;
            span {
              background: $secondary-color;
              color: #fff;
              text-transform: uppercase;
              font-size: 12px;
              padding: 2px 5px;
              border-radius: 3px;
              font-weight: 500;
              margin-left: 10px;
            }
          }
          .price {
            font-weight: 600;
            &.disabled {
              color: $button-background;
            }
          }
        }
      }
      .select {
        display: none;
      }
    }
    .mobile-actions {
      a {
        padding: 8px;
        border: none;
        background-color: $primary-color;
        border-radius: 30px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 2px solid #fff;
        position: relative;
        span {
          padding-left: 5px;
          text-transform: uppercase;
          color: #fff;
          font-size: 16px;
        }
      }

      .last-step-button {
        position: absolute;
        top: -50px;
        left: 0;
        width: 100%;
        a {
          border-radius: 0;
          background: $secondary-color;
          padding: 16px;
          height: auto;
          &:hover {
            background-color: #026d60;
          }

          .arrow-anim {
            width: 50px;
            height: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            .arrow {
              width: 10px;
              height: 10px;
              border: 2px solid;
              border-color: #fff transparent transparent #fff;
              transform: rotate(138deg);
            }
            .arrow-sliding {
              position: absolute;
              -webkit-animation: slide 4s linear infinite;
              animation: slide 4s linear infinite;
              &.delay1 {
                -webkit-animation-delay: 1s;
                animation-delay: 1s;
              }
              &.delay2 {
                -webkit-animation-delay: 2s;
                animation-delay: 2s;
              }
              &.delay3 {
                -webkit-animation-delay: 3s;
                animation-delay: 3s;
              }
            }
          }
        }
      }

      .arrow-buttons {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex: 1;
      }

      .back-button {
        width: 50px;
        position: fixed;
        bottom: 15px;
        left: 15px;
        z-index: 10;
        a {
          width: 100%;
          background-color: $button-background;
          padding: 0;
          .arrow {
            width: 9px;
            height: 9px;
            border-left: 2px solid #fff;
            border-top: 2px solid #fff;
            position: relative;
            transform: rotate(-45deg);
            margin: 0 6px;
            transition: all 0.3s ease;
            &::before {
              display: block;
              background-color: currentColor;
              width: 3px;
              transform-origin: bottom right;
              height: 2px;
              position: absolute;
              opacity: 0;
              bottom: -1.4px;
              transform: rotate(45deg);
              transition: all 0.3s ease;
              content: "";
              right: 0;
            }
          }
          &:hover {
            background-color: #4d6562;
            .arrow {
              transform: rotate(-45deg) translate(-4px, -4px);
              &::before {
                transform: rotate(45deg) translate(4px, 0px);
                opacity: 1;
                width: 15px;
              }
            }
          }
          img {
            padding-left: 0;
          }
          span {
            display: none;
          }
        }
      }

      .next-button {
        width: 150px;
        position: fixed;
        bottom: 15px;
        right: 15px;
        z-index: 10;
        a {
          width: 100%;
          flex-direction: row-reverse;
          .arrow {
            width: 9px;
            height: 9px;
            border-right: 2px solid #fff;
            border-bottom: 2px solid #fff;
            position: relative;
            transform: rotate(-45deg);
            margin: 0 6px;
            transition: all 0.3s ease;
            &::before {
              display: block;
              background-color: currentColor;
              width: 3px;
              transform-origin: bottom right;
              height: 2px;
              position: absolute;
              opacity: 0;
              bottom: calc(-2px / 2);
              transform: rotate(45deg);
              transition: all 0.3s ease;
              content: "";
              right: 0;
            }
          }
          &:hover {
            background-color: #fa5a28;
            .arrow {
              transform: rotate(-45deg) translate(4px, 4px);
              &::before {
                opacity: 1;
                width: 15px;
              }
            }
          }
          img {
            padding-left: 5px;
          }
          span {
            padding-left: 0;
            padding-right: 0;
          }
        }
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .custom-made-cart-wrapper {
    .cart-content-wrapper {
      .popup-on-mobile {
        width: 400px;
        left: auto;
        right: 0;
        height: auto;
      }
      &.active {
        .popup-on-mobile {
          // top: 50px;
        }
      }
    }
  }
}
@media screen and (min-width: 900px) {
  .popup-sword-part {
    .middle-content {
      max-width: 750px;
      .top-bar-wrapper {
        ul {
          li {
            flex-basis: calc(25% - 10px);
          }
        }
      }
    }
  }

  .popup-confirmation {
    .middle-col {
      max-width: 600px;
    }
  }

  // .custom-made-cart-wrapper {
  //   &.steps-screens {
  //     padding-top: 7px;
  //   }
  // }
}

@media screen and (min-width: 1200px) {
  .com-btn {
    padding: 15px 20px;
    transition: all 0.3s ease-in-out;
    &:hover {
      transition: all 0.3s ease-in-out;
      background-color: #fa5a28;
    }
  }
  .custom-made-cart-wrapper {
    margin-bottom: 60px;
    .cart-main-wrapper {
      .view-all-button {
        display: flex;
        .icon {
          // display: none;
        }
      }
    }
    .header-wrapper {
      p {
        max-width: 62%;
        margin: 0 auto;
        line-height: 30px;
        font-size: 19px;
      }
      h1 {
        font-size: 40px;
      }
      a {
        display: none;
      }
    }

    .cart-content-wrapper {
      &::after {
        display: none;
      }
      .popup-on-mobile {
        position: sticky;
        top: 120px;
        width: 300px;
        visibility: visible;
        opacity: 1;
        bottom: auto;
        left: auto;
        padding: 0;
        background: transparent;
        display: block;
        z-index: auto;
        box-shadow: 0 0 0;
      }

      .mobile-actions {
        position: unset;
        bottom: auto;
        display: flex;
        background: transparent;
        padding: 0;
        border: none;
        a {
          padding: 10px 15px;
          height: auto;
          transition: all 0.3s ease-in-out;
          line-height: 20px;
          border: none;
          border-radius: 0;
          &:hover {
            transition: all 0.3s ease-in-out;
            background: $text-color;
          }
        }
        .back-button {
          position: inherit;
          z-index: auto;
          width: 50%;
          a {
            padding: 10px 15px;
            span {
              display: flex;
            }
          }
        }
        .last-step-button {
          position: unset;
          top: auto;
          width: max-content;

          a {
            border-radius: 3px;
            padding: 15px;
          }
        }
        .next-button {
          position: inherit;
          z-index: auto;
          width: 50%;
        }
      }
    }

    .cart-alert-wrapper {
      padding: 20px;
      margin-top: 20px;
      margin-bottom: 30px;
      border-radius: 5px;
      .icon {
        width: 70px;
      }
    }
  }
}

.cart-main-wrapper {
  border: 1px solid #b3bdbc;
  border-radius: 3px;
  overflow: hidden;
  color: $text-color;
  background: #fff;
  width: 100%;
  height: 100%;
  .mob-header {
    padding: 10px;
    background: $disabled-color;
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: center;
    font-weight: 600;
    color: $text-color;
    border-bottom: 1px solid #b3bdbc;
  }
  .empty-view {
    text-align: center;
    padding: 40px 20px;
    display: none;
    h3 {
      display: none;
    }
    .animated-icon {
      display: none;
    }
    img {
      width: 60%;
      margin: 0 auto;
    }
  }
  .content-area {
    .empty-text {
      text-align: center;
      padding: 10vh 20px;
      color: $button-background;
    }

    .expanded-rows {
      // height: 100%;
    }
    .single-item-row {
      margin-bottom: 8px;
      .row-head {
        background: $row-back;
        padding: 12px 10px;
        font-weight: 700;
        font-size: 19px;
        line-height: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        opacity: 0.9;
        &.active {
          opacity: 1;
          .icon {
            opacity: 1;
            span {
              &:last-child {
                transition: all 0.3s ease-in-out;
                transform: rotate(0deg);
              }
            }
          }
        }
        .icon {
          background-color: $text-color;
          width: 18px;
          height: 18px;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          opacity: 0.5;
          transition: all 0.3s ease;
          &:hover {
            transition: all 0.3s ease;
            background-color: #4d6562;
            opacity: 1;
          }
          span {
            display: block;
            height: 2px;
            width: 50%;
            background: #fff;
            position: absolute;
            transition: all 0.3s ease-in-out;
            &:last-child {
              transform: rotate(90deg) translateY(00px);
            }
          }
        }
      }

      .row-content {
        padding: 10px;
        overflow-y: auto;
        display: none;
        .inner-row {
          margin-bottom: 10px;
          // border-bottom: 1px solid $disabled-color;
          // padding-bottom: 10px;
          &.remove-border {
            border: 0px;
            margin-bottom: 0;
          }
          .group-title {
            font-weight: 600;
            margin-bottom: 20px;
          }
          &:last-child {
            border-bottom: none;
            margin-bottom: 0;
          }
          .wrap-top-title {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 8px;
            margin-top: 15px;
            a {
              display: block;
              display: flex;
              align-items: center;
              gap: 5px;
              border: 1px solid $cart-color;
              font-size: 12px;
              border-radius: 3px;
              text-transform: uppercase;
              text-decoration: none;
              color: $cart-color;
              padding: 2px 5px;
              transition: 0.3s ease-in-out;
              &:hover {
                background-color: $disabled-color;
              }
              span {
                line-height: 16px;
              }
            }
          }
          .item-title {
            font-weight: 600;
          }
          .desc-price {
            display: flex;
            align-items: center;
            justify-content: space-between;
          }
        }
        img {
          max-width: 100px;
        }
      }
    }
    .total-view {
      background-color: $row-back;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 13px 15px;
      font-size: 19px;
      font-weight: 600;
      text-transform: none;
    }
  }
}

