    &:hover {
      transition: all 0.3s ease-in-out;
      box-shadow: 4px 5px 18px 3px rgba(0, 0, 0, 0.08);
    }
    &.active {
      border-color: $primary-color;
      .price {
        color: $primary-color;
      }
    }
    &.not-available {
      opacity: 0.6;
      pointer-events: none;
      position: relative;
      span.not-available-text {
        display: block;
        width: 90%;
        background: #f3f6f9;
        color: #b3bdbc;
        text-transform: uppercase;
        padding: 4px 11px;
        font-size: 10px;
        font-weight: 700;
        text-align: center;
        position: absolute;
        top: 10px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 5;
        line-height: 11px;
      }
