      &:nth-of-type(2n) {
        .selection-bottom {
          .name-info {
            .info {
              .tooltip-content {
                left: auto;
                transform: inherit;
                right: -25px;
                &::after {
                  left: 86%;
                }
              }
            }
          }
        }
      }
    }

