        place-items: center;
        border-radius: 50%;
        background-color: $disabled-color;
        border: none;
        cursor: pointer;
        transition: all 0.3s ease-in-out;
        &:hover {
          transition: all 0.3s ease-in-out;
          background: #4f6462;
          svg {
            transition: all 0.3s ease-in-out;
            fill: #fff;
            opacity: 1;
          }
          &.count {
            span {
              transition: all 0.3s ease-in-out;
              color: #fff;
              box-shadow: -2px 2px 0 0 #fff;
            }
          }
        }
        &.count {
          transition: all 0.3s ease-in-out;
          display: flex;
          align-items: center;
          justify-content: center;
          span {
            transition: all 0.3s ease-in-out;
            display: block;
            width: 12px;
            height: 12px;
            border: 1px solid $button-background;
            font-size: 10px;
            line-height: 10px;
            color: #393e3e;
            font-weight: 500;
            box-shadow: -1px 1px 0 0 $button-background;
          }
        }
        &.view-images {
          padding: 0;
          svg {
            width: 18px;
            height: 18px;
            margin: 0 auto;
          }
        }
        svg {
          fill: $text-color;
          opacity: 0.7;
          transition: all 0.3s ease-in-out;
        }
      }
    }

    .selection-bottom {
      padding: 10px;
      flex: 1 1 auto;
      display: flex;
      flex-direction: column;
      .top-selec {
        border-bottom: 1px solid $disabled-color;
        padding: 5px 0 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .price {
          font-weight: 500;
        }
      }

      .name-info {
        padding: 16px 0 6px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        flex: 1 1 auto;
        label {
          font-size: 15px;
          font-weight: 500;
        }
        .info {
          position: relative;
          cursor: pointer;
          &:hover {
            .tooltip-content {
              top: 30px;
              opacity: 1;
              visibility: visible;
              transition: all 0.3s ease-in-out;
            }
          }
          .icon {
            width: 20px;
            height: 20px;
            display: flex;
            place-items: center;
            border-radius: 50%;
            svg {
              width: 100%;
              height: 100%;
              fill: $cart-color;
            }
          }

          .tooltip-content {
            position: absolute;
            top: 50px;
            width: 250px;
            left: 50%;
            transform: translateX(-50%);
            background-color: rgba(0, 0, 0, 0.8);
            padding: 15px;
            color: #fff;
            z-index: 8;
            opacity: 0;
            visibility: hidden;
            transition: all 0.3s ease-in-out;
            &:after {
              content: "";
              position: absolute;
              bottom: 100%;
              left: 50%;
              margin-left: -8px;
              width: 0;
              height: 0;
              border-bottom: 8px solid rgba(0, 0, 0, 0.8);
              border-right: 8px solid transparent;
              border-left: 8px solid transparent;
            }
            p {
              margin: 0;
              padding: 0;
              font-size: 14px;
              font-weight: 100;
              line-height: 23px;
            }
          }
        }
      }
    }
  }
}

