.portfolio-page {
  position: relative;
  &__container {
    position: relative;
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    @include response(1200px) {
      display: block;
    }
  }
  &__head-left {
    position: relative;
    flex: 0 0 620px;
    max-width: 620px;
    width: 100%;
    margin-right: 150px;
    @include response(1200px) {
      margin-right: 0;
      max-width: 100%;
    }
  }
  &__title {
    position: relative;
    margin-bottom: 48px;
    display: flex;
    align-items: center;
    @include response(1200px) {
      margin-bottom: 18px;
    }
  }
  &__title-img {
    position: relative;
    margin-right: 24px;
    flex: 0 0 auto;
  }
  &__title-text {
    position: relative;
    margin: 0;
    font-size: 32px;
    font-weight: 700;
    line-height: 35px;
    @include response(1200px) {
      font-size: 25px;
      line-height: 30px;
    }
  }
  &__mobile-img {
    display: none;
    @include response(1200px) {
      display: block;
      margin-left: -16px;
      margin-right: -16px;
      margin-bottom: 16px;
      img {
        width: 100%;
      }
    }
  }
  &__head-info {
    position: relative;
    margin-bottom: 48px;
    padding-left: 48px;
    border-left: 3px solid #80918f;
    @include response(1200px) {
      padding-left: 22px;
      margin-bottom: 24px;
    }
  }
  &__head-row {
    position: relative;
    margin-bottom: 24px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__head-triggers {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    @include response(1200px) {
      margin-bottom: -16px;
    }
  }
  &__head-triggers-item {
    position: relative;
    margin-right: 32px;
    @include response(1200px) {
      margin-right: 24px;
      margin-bottom: 16px;
    }
  }
  &__head-triggers-head {
    position: relative;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;
    color: #4d6562;
    img {
      flex: 0 0 19px;
      margin-right: 8px;
    }
  }
  &__head-triggers-title {
    font-size: 19px;
    font-weight: 700;
    line-height: 30px;
  }
  &__head-text {
    position: relative;
    font-size: 17px;
    font-weight: 400;
    line-height: 26px;
    color: #4d6562;
  }
  &__head-link {
    position: relative;
  }
  &__head-link-title {
    font-size: 16px;
    font-weight: 700;
    line-height: 25px;
    color: #01231f;
  }
  &__head-link-val {
    position: relative;
    display: flex;
    align-items: center;
    font-family: Public Sans;
    font-size: 17px;
    font-weight: 400;
    line-height: 26px;
    color: #000;
    @include desktop-hover() {
      text-decoration: none;
    }
    svg {
      flex: 0 0 auto;
      margin-right: 8px;
    }
  }
  &__head-controls {
    position: relative;
    display: flex;
    gap: 20px;
    @include response(767px) {
      display: block;
    }
  }
  &__btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: Hug (241px) px;
    padding: 12px 20px;
    border-radius: 2px;
    color: #fff;
    background: #4d6562;
    text-decoration: none;
    font-family: "Public Sans";
    font-size: 17px;
    font-weight: 700;
    line-height: 26px;
    transition: 0.3s ease-in-out;
    @include response(767px) {
      margin-bottom: 8px;
      display: inline-flex;
      vertical-align: top;
      &:last-child {
        margin-bottom: 0;
      }
    }
    svg {
      flex: 0 0 auto;
      margin-left: 6px;
    }
    @include desktop-hover() {
      background: #ff3d00;
    }
    &.red {
      background: #ff3d00;
      @include desktop-hover() {
        background: #4d6562;
      }
    }
  }
  &__head-right {
    position: relative;
    flex: 0 0 auto;
    @include response(1200px) {
      display: none;
    }
  }
  &__process {
    position: relative;
    padding-top: 80px;
    padding-bottom: 80px;
    background: #f0f2f2;
    @include response(1200px) {
      padding-top: 52px;
      padding-bottom: 48px;
    }
    &--green {
      background: #4d6562;
    }
  }
  &__head-row-title {
    position: relative;
    margin-bottom: 48px;
    border-bottom: 1px solid #80918f;
    padding-bottom: 4px;
    font-size: 22px;
    font-weight: 700;
    line-height: 26px;
    @include response(1200px) {
      margin-bottom: 32px;
    }
    .portfolio-page__process--green & {
      color: #fff;
    }
  }
  &__process-list {
    position: relative;
    margin-left: -40px;
    display: flex;
    @include response(1200px) {
      margin-left: -16px;
      margin-right: -16px;
      padding-left: 16px;
      padding-right: 16px;
      overflow-x: auto;
    }
  }
  &__process-item {
    position: relative;
    margin-left: 40px;
    width: 100%;
    @include response(1200px) {
      margin-left: 0;
      margin-right: 16px;
      flex: 0 0 86%;
    }
    img {
      width: 100%;
    }
  }
  &__characteristic {
    position: relative;
  }
  &__characteristic-item {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-bottom: 80px;
    padding-top: 80px;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 32px;
      flex-direction: column-reverse;
    }
    &::before {
      content: "";
      position: absolute;
      height: 1px;
      width: 100vw;
      background: #80918f;
      left: 50%;
      margin-left: -50vw;
      bottom: 0;
    }
  }
  &__characteristic-left {
    position: relative;
    flex: 0 0 510px;
    max-width: 510px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      flex: 0 0 auto;
    }
  }
  &__characteristic-title {
    position: relative;
    font-size: 32px;
    font-weight: 700;
    line-height: 35.2px;
    margin-bottom: 40px;
    padding-bottom: 8px;
    border-bottom: 2px solid #80918f;
    @include response(1200px) {
      border-width: 1px;
      font-size: 28px;
      line-height: 33px;
      margin-bottom: 24px;
      display: none;
    }
    &--mobile {
      display: none;
      @include response(1200px) {
        display: block;
      }
    }
  }
  &__characteristic-list {
    position: relative;
  }
  &__characteristic-row {
    position: relative;
    margin-bottom: 16px;
    border-bottom: 1px solid #b3bdbc;
    &:last-child {
      margin-bottom: 0;
      border-bottom: 0;
    }
  }
  &__characteristic-row-title {
    font-size: 16px;
    font-weight: 700;
    line-height: 21.6px;
    position: relative;
    margin-bottom: 2px;
  }
  &__characteristic-row-value {
    font-size: 16px;
    font-weight: 400;
    line-height: 21.6px;
    padding-bottom: 8px;
  }
  &__characteristic-right {
    position: relative;
    @include response(1200px) {
      margin-bottom: 24px;
    }
  }
  &__characteristic-imgs {
    position: relative;
  }
  &__characteristic-imgs-item {
    position: relative;
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__projects {
    position: relative;
    padding-top: 64px;
  }
}
