.rate {
  position: relative;
  &__default {
    display: flex;
  }
  &__item {
    width: 16px;
    height: 16px;
    fill: #b3bdbc;
  }
  &__active {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    svg {
      fill: #ff3d00;
    }
  }
  &__item-holder {
    position: relative;
    width: 16px;
    height: 16px;
  }
  &__item-holder-inner {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
  }
}
