.popup-sword-part,
.popup-confirmation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: rgba(0, 0, 0, 0.4);
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.popup-sword-part.active,
.popup-confirmation.active {
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease-in-out;
}
.popup-sword-part .middle-content,
.popup-sword-part .middle-col,
.popup-confirmation .middle-content,
.popup-confirmation .middle-col {
  width: 90%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  padding: 20px;
  max-height: 80vh;
  overflow-y: auto;
}
.popup-sword-part .middle-content .top-bar-wrapper,
.popup-sword-part .middle-col .top-bar-wrapper,
.popup-confirmation .middle-content .top-bar-wrapper,
.popup-confirmation .middle-col .top-bar-wrapper {
