  align-items: flex-start;
  gap: 15px;
}
.popup-sword-part .middle-content .top-bar-wrapper ul,
.popup-sword-part .middle-col .top-bar-wrapper ul,
.popup-confirmation .middle-content .top-bar-wrapper ul,
.popup-confirmation .middle-col .top-bar-wrapper ul {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 0;
  padding: 0;
  flex-direction: row;
  flex-wrap: wrap;
  flex-grow: 1;
}
.popup-sword-part .middle-content .top-bar-wrapper ul li,
.popup-sword-part .middle-col .top-bar-wrapper ul li,
.popup-confirmation .middle-content .top-bar-wrapper ul li,
.popup-confirmation .middle-col .top-bar-wrapper ul li {
  flex-basis: calc(50% - 10px);
  text-align: center;
  border: 1px solid #dcdcdc;
  text-transform: uppercase;
  font-weight: 600;
  padding: 8px 5px;
  transition: all 0.3s ease-in-out;
  color: #80918f;
}
.popup-sword-part .middle-content .top-bar-wrapper ul li.active, .popup-sword-part .middle-content .top-bar-wrapper ul li:hover,
.popup-sword-part .middle-col .top-bar-wrapper ul li.active,
.popup-sword-part .middle-col .top-bar-wrapper ul li:hover,
.popup-confirmation .middle-content .top-bar-wrapper ul li.active,
.popup-confirmation .middle-content .top-bar-wrapper ul li:hover,
.popup-confirmation .middle-col .top-bar-wrapper ul li.active,
.popup-confirmation .middle-col .top-bar-wrapper ul li:hover {
