  background-color: #0d9a88;
  border-color: #0d9a88;
  color: #fff;
  cursor: pointer;
}
.popup-sword-part .middle-content .top-bar-wrapper ul li span,
.popup-sword-part .middle-col .top-bar-wrapper ul li span,
.popup-confirmation .middle-content .top-bar-wrapper ul li span,
.popup-confirmation .middle-col .top-bar-wrapper ul li span {
