body {
  height: 100%;
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
  font-family: "Public Sans", sans-serif;
  @include response(1200px) {
    ::-webkit-scrollbar {
      display: none;
    }
  }
}

.wrapp {
  width: 100%;
  overflow-x: hidden;
  padding-top: 141px;
  @include response(1200px) {
    padding-top: 56px;
  }
  &--ovi {
    overflow: inherit;
    @include response(1200px) {
      overflow: hidden;
    }
  }
}

.container {
  max-width: 1310px;
  padding: 0 15px;
  margin: 0 auto;
}

* {
  outline: none;
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
