.main-info {
  position: relative;
  &__first {
    position: relative;
    background: #f0f2f2;
    padding-top: 80px;
    padding-bottom: 80px;
    @include response(1200px) {
      padding-top: 30px;
      padding-bottom: 40px;
      .main-info__container {
        display: flex;
        flex-direction: column;
      }
    }
  }
  &__container {
    max-width: 1280px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__first-title {
    position: relative;
    margin-bottom: 16px;
    padding-bottom: 16px;
    font-size: 40px;
    font-weight: 700;
    line-height: 44px;
    text-align: center;
    @include response(1200px) {
      font-size: 32px;
      line-height: 35px;
    }
    &::before {
      content: "";
      position: absolute;
      width: 620px;
      height: 1px;
      background: #b3bdbc;
      left: 50%;
      bottom: 0;
      margin-left: -310px;
      @include response(1200px) {
        margin-left: 0;
        left: 0;
        width: 100%;
      }
    }
  }
  &__first-text {
    position: relative;
    margin-bottom: 24px;
    font-size: 19px;
    font-weight: 500;
    line-height: 30px;
    text-align: center;
    color: #01231f;
    @include response(1200px) {
      font-size: 17px;
      line-height: 26px;
    }
  }
  &__first-btn-box {
    text-align: center;
    position: relative;
    z-index: 1;
    @include response(1200px) {
      order: 4;
    }
  }
  &__first-btn {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 13px 24px;
    background: #ff3d00;
    font-weight: 700;
    font-size: 16px;
    border-radius: 2px;
    line-height: 1;
    color: #fff;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: #e03702;
    }
    svg {
      flex: 0 0 auto;
      margin-right: 8px;
    }
  }
  &__first-img {
    position: relative;
    margin-top: -48px;
    @include response(1200px) {
      margin-top: 0;
      margin-bottom: 35px;
    }
    picture {
      position: relative;
    }
  }
  &__first-decor {
    position: absolute;
    left: -135px;
    right: -135px;
    top: -130px;
    @include response(1200px) {
      left: -56px;
      right: -56px;
      top: 45%;
      transform: translateY(-50%) rotate(-4deg);
    }
  }
  &__triggers {
    position: relative;
    background: #4d6562;
    padding-top: 48px;
    padding-bottom: 48px;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 32px;
    }
  }
  &__triggers-list {
    position: relative;
    display: flex;
    justify-content: center;
    gap: 96px;
    @include response(1200px) {
      display: block;
    }
  }
  &__triggers-item {
    width: 100%;
    position: relative;
    @include response(1200px) {
      padding-bottom: 24px;
      margin-bottom: 24px;
      border-bottom: 1px solid #80918f;
    }
    &:last-child {
      @include response(1200px) {
        border-bottom: 0;
        margin-bottom: 0;
        padding-bottom: 0;
      }
      &::before {
        display: none;
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      width: 1px;
      background: #80918f;
      right: -48px;
      @include response(1200px) {
        display: none;
      }
    }
  }
  &__triggers-item-head {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
  }
  &__triggers-item-icon {
    position: relative;
    display: flex;
    flex: 0 0 36px;
    margin-right: 12px;
  }
  &__triggers-item-title {
    position: relative;
    font-size: 22px;
    font-weight: 700;
    line-height: 26px;
    color: #fff;
  }
  &__triggers-item-text {
    font-size: 16px;
    font-weight: 400;
    line-height: 25px;
    color: #fff;
    p {
      margin: 0;
    }
  }
  &__brands {
    position: relative;
    padding-top: 60px;
    padding-bottom: 150px;
    @include response(1200px) {
      padding-top: 48px;
      padding-bottom: 50px;
    }
  }
  &__brands-title {
    position: relative;
    margin-bottom: 20px;
    font-size: 32px;
    font-weight: 700;
    line-height: 35px;
    text-align: center;
    @include response(1200px) {
      font-size: 28px;
      line-height: 33px;
    }
  }
  &__brands-text {
    position: relative;
    padding-left: 72px;
    padding-right: 72px;
    font-size: 16px;
    font-weight: 400;
    line-height: 25px;
    text-align: center;
    color: #4d6562;
    margin-bottom: 37px;
    @include response(1200px) {
      padding-left: 0;
      padding-right: 0;
      margin-bottom: 20px;
    }
    p {
      margin: 0;
    }
  }
  &__brands-list {
    position: relative;
    display: flex;
    justify-content: center;
    gap: 24px;
    @include response(1200px) {
      display: block;
    }
  }
  &__brands-item {
    position: relative;
    width: 33.333%;
    flex: 0 0 33.333%;
    @include response(1200px) {
      width: 100%;
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__brands-item-label {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 6px 10px;
    vertical-align: top;
    color: #fff;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    img {
      flex: 0 0 16px;
      margin-right: 4px;
    }
  }
  &__brands-item-inner {
    position: relative;
    border: 1px solid #b3bdbc;
    padding: 24px 32px 0;
    @include response(1200px) {
      padding: 30px 24px 0;
    }
  }
  &__brands-item-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 8px;
  }
  &__brands-item-left {
    position: relative;
    padding-right: 10px;
  }
  &__brands-item-title {
    position: relative;
    margin-bottom: 8px;
    font-size: 22px;
    font-weight: 700;
    line-height: 26px;
    color: #01231f;
  }
  &__brands-item-rate {
    position: relative;
    display: flex;
    align-items: center;
  }
  &__brands-item-rate-title {
    position: relative;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    color: #4d6562;
    margin-left: 8px;
  }
  &__brands-item-img {
    position: relative;
    flex: 0 0 64px;
  }
  &__brands-item-text {
    font-size: 16px;
    font-weight: 400;
    line-height: 28px;
    color: #4d6562;
    margin-bottom: 24px;
    p {
      margin: 0;
    }
  }
  &__brands-item-list {
    position: relative;
    margin-bottom: 32px;
  }
  &__brands-item-row {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__brands-item-row-icon {
    position: relative;
    flex: 0 0 auto;
    margin-right: 8px;
    display: flex;
  }
  &__brands-item-row-title {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: #4d6562;
    margin-right: 4px;
  }
  &__brands-item-row-text {
    position: relative;
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    color: #01231f;
  }
  &__brands-item-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 24px;
    padding: 12px;
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
    border-radius: 2px;
    background: #ff3d00;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: #e03702;
    }
    svg {
      flex: 0 0 auto;
      margin-left: 6px;
    }
  }
  &__brands-item-more {
    position: relative;
    cursor: pointer;
    font-size: 14px;
    font-weight: 700;
    line-height: 19px;
    text-align: center;
    color: #4d6562;
    padding: 7px;
    background: #f0f2f2;
    border-top: 1px solid #b3bdbc;
    margin-left: -32px;
    margin-right: -32px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      margin-left: -24px;
      margin-right: -24px;
    }
    @include desktop-hover() {
      background: #e6e9e9;
    }
    svg {
      margin-left: 4px;
    }
    span {
      &:nth-child(2) {
        display: none;
      }
    }
    &.active {
      span {
        &:nth-child(1) {
          display: none;
        }
        &:nth-child(2) {
          display: block;
        }
      }
    }
  }
  &__brands-item-more-text {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    border: 1px solid #b3bdbc;
    border-top: 0;
    padding: 20px 32px;
    box-shadow: 0px 6px 12px 0px #01231f1a;
    font-size: 16px;
    font-weight: 400;
    line-height: 25px;
    display: none;
    z-index: 1;
    background: #fff;
    @include response(1200px) {
      padding: 16px;
    }
    p {
      margin: 0;
    }
  }
  &__archive {
    position: relative;
    padding-top: 20px;
    padding-bottom: 80px;
    @include response(1200px) {
      padding-left: 24px;
      padding-right: 24px;
      padding-bottom: 48px;
    }
  }
  &__archive-title {
    position: relative;
    margin-bottom: 48px;
    font-size: 32px;
    font-weight: 700;
    line-height: 35px;
    text-align: center;
    color: #01231f;
    @include response(1200px) {
      margin-bottom: 32px;
      font-size: 28px;
      line-height: 33px;
    }
  }
  &__archive-slider {
    margin-bottom: 72px;
    max-width: 620px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    .slick-list {
      overflow: visible;
    }
    .slick-slide {
      transition: 0.3s ease-in-out;
    }
    .slick-slide:not(.slick-current) {
      pointer-events: none;
      opacity: 0.5;
      transform: scale(0.8);
      @include response(1200px) {
        transform: scale(0.9);
      }
    }
    .slick-arrow {
      position: absolute;
      width: 48px;
      height: 48px;
      z-index: 1;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 50%;
      margin-top: -24px;
      left: -330px;
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        left: -40px;
        width: 32px;
        height: 32px;
        margin-top: -16px;
      }
      @include desktop-hover() {
        svg {
          transform: translateX(-3px);
        }
      }
      svg {
        transition: 0.3s ease-in-out;
        @include response(1200px) {
          width: 12px;
          height: 24px;
        }
      }
      &.slick-next {
        left: auto;
        right: -330px;
        @include response(1200px) {
          right: -40px;
        }
        @include desktop-hover() {
          svg {
            transform: translateX(3px) scaleX(-1);
          }
        }
        svg {
          transform: scaleX(-1);
        }
      }
      &.slick-disabled {
        opacity: 0;
        pointer-events: none;
      }
    }
    .slick-dots {
      position: absolute;
      left: 0;
      right: 0;
      bottom: -40px;
      display: flex;
      align-items: center;
      justify-content: center;
      list-style: none;
      padding: 0;
      margin: 0;
      li {
        flex: 0 0 auto;
        display: flex;
        transition: 0.3s ease-in-out;
        &.slick-active {
          margin-left: 4px;
          margin-right: 4px;
          button {
            &::before {
              width: 16px;
              margin-left: -8px;
              background: #01231f;
            }
          }
        }
      }
      button {
        position: relative;
        width: 16px;
        height: 8px;
        padding: 0;
        font-size: 0;
        border: 0;
        background: transparent;
        @include desktop-hover() {
          &::before {
            background: #01231f;
          }
        }
        &::before {
          content: "";
          position: absolute;
          background: #b3bdbc;
          width: 8px;
          height: 8px;
          border-radius: 10px;
          top: 0;
          left: 50%;
          margin-left: -4px;
          transition: 0.3s ease-in-out;
        }
      }
    }
  }
  &__archive-item {
    position: relative;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      .main-info__archive-item-img {
        img {
          transform: scale(1.03);
        }
      }
    }
  }
  &__archive-item-img {
    position: relative;
    border-radius: 2px 2px 0 0;
    overflow: hidden;
    img {
      width: 100%;
      transition: 0.6s ease-in-out;
    }
  }
  &__archive-item-body {
    position: relative;
    border: 1px solid #b3bdbc;
    background: #fff;
    padding: 12px 16px;
    border-radius: 0 0 2px 2px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    opacity: 0;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      padding-top: 8px;
      padding-bottom: 8px;
    }
    .slick-current & {
      opacity: 1;
    }
  }
  &__archive-item-left {
    position: relative;
    padding-right: 10px;
  }
  &__archive-item-description-title {
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    color: #4d6562;
    @include response(1200px) {
      font-size: 12px;
      line-height: 18px;
    }
  }
  &__archive-item-name {
    font-size: 17px;
    font-weight: 700;
    line-height: 26px;
    color: #4d6562;
    @include response(1200px) {
      font-size: 15px;
      line-height: 23px;
    }
  }
  &__archive-item-right {
    position: relative;
    border-left: 1px solid #b3bdbc;
    padding-left: 15px;
  }
  &__archive-item-price-title {
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    color: #4d6562;
    @include response(1200px) {
      font-size: 12px;
      line-height: 18px;
    }
  }
  &__archive-item-price-val {
    font-size: 19px;
    font-weight: 700;
    line-height: 30px;
    color: #4d6562;
    @include response(1200px) {
      font-size: 17px;
      line-height: 26px;
    }
  }
  &__archive-btn-box {
    text-align: center;
  }
  &__archive-btn {
    position: relative;
    display: inline-block;
    vertical-align: top;
    color: #fff;
    font-size: 17px;
    font-weight: 700;
    line-height: 26px;
    text-align: center;
    padding: 12px 32px;
    background: #4d6562;
    text-decoration: none;
    border-radius: 2px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: #80918f;
    }
  }
  &__testimonials {
    position: relative;
    padding-top: 60px;
    padding-bottom: 60px;
    @include response(1200px) {
      padding-top: 48px;
      padding-bottom: 48px;
    }
  }
  &__testimonials-title {
    position: relative;
    margin-bottom: 40px;
    font-size: 32px;
    font-weight: 700;
    line-height: 35px;
    text-align: center;
    @include response(1200px) {
      margin-bottom: 32px;
      font-size: 28px;
      line-height: 33px;
    }
  }
  &__testimonials-slider {
    max-width: 1060px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    .slick-dots {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 40px;
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      li {
        flex: 0 0 auto;
        display: flex;
        transition: 0.3s ease-in-out;
        &.slick-active {
          margin-left: 4px;
          margin-right: 4px;
          button {
            &::before {
              width: 16px;
              margin-left: -8px;
              background: #fff;
            }
          }
        }
      }
      button {
        position: relative;
        width: 16px;
        height: 8px;
        padding: 0;
        font-size: 0;
        border: 0;
        background: transparent;
        @include desktop-hover() {
          &::before {
            background: #fff;
          }
        }
        &::before {
          content: "";
          position: absolute;
          background: #b3bdbc;
          width: 8px;
          height: 8px;
          border-radius: 10px;
          top: 0;
          left: 50%;
          margin-left: -4px;
          transition: 0.3s ease-in-out;
        }
      }
    }
    .slick-arrow {
      position: absolute;
      width: 24px;
      height: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      z-index: 1;
      top: 50%;
      left: 20px;
      margin-top: -20px;
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        top: auto;
        bottom: 32px;
        margin-top: 0;
        left: 27px;
      }
      @include desktop-hover() {
        svg {
          transform: translateX(-2px);
        }
      }
      svg {
        fill: #fff;
      }
      &.slick-next {
        left: auto;
        right: 20px;
        @include response(1200px) {
          right: 27px;
        }
        @include desktop-hover() {
          svg {
            transform: scaleX(-1) translateX(-2px);
          }
        }
        svg {
          transform: scaleX(-1);
          transition: 0.3s ease-in-out;
        }
      }
    }
  }
  &__testimonials-item {
    position: relative;
    padding: 76px 110px 116px;
    background: #01231f;
    display: flex !important;
    align-items: center;
    justify-content: space-between;
    border-radius: 2px;
    @include response(1200px) {
      padding: 32px 32px 88px;
      display: block !important;
    }
  }
  &__testimonials-item-left {
    position: relative;
    max-width: 486px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 48px;
    }
  }
  &__testimonials-item-text {
    position: relative;
    color: #fff;
    font-size: 18px;
    font-style: italic;
    font-weight: 400;
    line-height: 28px;
    margin-bottom: 32px;
    p {
      margin: 0;
    }
  }
  &__testimonials-item-author {
    position: relative;
    display: flex;
    align-items: center;
    @include response(1200px) {
      display: block;
      padding-right: 50px;
    }
  }
  &__testimonials-item-author-icon {
    position: relative;
    display: flex;
    flex: 0 0 auto;
    margin-right: 32px;
    @include response(1200px) {
      margin: 0;
      position: absolute;
      top: 0;
      right: 0;
    }
  }
  &__testimonials-item-author-box {
    position: relative;
  }
  &__testimonials-item-author-name {
    position: relative;
    color: #fff;
    font-size: 19px;
    font-weight: 700;
    line-height: 29px;
  }
  &__testimonials-item-author-chanel {
    position: relative;
    display: flex;
    align-items: center;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    @include response(1200px) {
      display: block;
    }
    a {
      margin-left: 4px;
      display: flex;
      align-items: center;
      color: #fff;
      @include response(1200px) {
        margin-left: 0;
      }
      @include desktop-hover() {
        text-decoration: none;
      }
      svg {
        flex: 0 0 auto;
        margin-right: 4px;
      }
    }
  }
  &__testimonials-item-right {
    position: relative;
    flex: 0 0 290px;
  }
  &__testimonials-item-link {
    display: flex;
    border-radius: 2px;
    overflow: hidden;
    img {
      width: 100%;
    }
    @include desktop-hover() {
      span {
        background: #000;
        svg {
          transform: scale(1.3);
        }
      }
    }
    span {
      position: absolute;
      width: 96px;
      height: 96px;
      border-radius: 10px;
      background: rgba(#000, 0.6);
      top: 50%;
      left: 50%;
      margin-top: -48px;
      margin-left: -48px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: 0.3s ease-in-out;
      svg {
        transition: 0.3s ease-in-out;
      }
    }
  }
}
