.upgrades {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-left: -40px;
  @include response(767px) {
    margin-left: 0;
    display: block;
  }
  &__item {
    position: relative;
    flex: 0 0 calc(50% - 40px);
    max-width: calc(50% - 40px);
    width: 100%;
    margin-bottom: 32px;
    margin-left: 40px;
    border: 1px solid #d4dbe2;
    transition: 0.3s ease-in-out;
    @include response(767px) {
      margin-left: 0;
      margin-bottom: 16px;
      max-width: 100%;
    }
    &.active {
      border-color: #ff3d00;
    }
    &.disabled {
      pointer-events: none;
    }
  }
  &__item-head {
    position: relative;
    padding: 16px 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #d4dbe2;
    .disabled & {
      opacity: 0.5;
    }
  }
  &__item-left {
    position: relative;
    padding-right: 10px;
  }
  &__item-right {
    position: relative;
    flex: 0 0 auto;
  }
  &__item-price {
    position: relative;
    font-size: 17px;
    font-weight: 700;
    line-height: 26px;
    text-align: right;
    &.red {
      color: #ff3d00;
    }
  }
  &__item-text {
    position: relative;
    padding: 16px 12px;
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;
    color: #01231f;
    .disabled & {
      opacity: 0.5;
    }
    p {
      margin: 0;
    }
  }
  &__item-attention {
    position: relative;
    display: flex;
    align-items: center;
    color: #01231f;
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    padding: 12px 16px;
    background: #fff7e3;
    svg {
      flex: 0 0 auto;
      margin-right: 8px;
    }
  }
  &__item-left-text {
    position: relative;
    font-size: 16px;
    font-weight: 700;
    line-height: 30px;
  }
  &__item-time {
    position: relative;
    font-size: 17px;
    font-weight: 400;
    line-height: 26px;
    text-align: right;
    span {
      color: #ff3d00;
      font-weight: 700;
    }
  }
}

.upgrades-input {
  position: relative;
  input {
    display: none;
    &:checked + label {
      &::after {
        left: 16px;
      }
      &::before {
        background: #ff3d00;
      }
    }
  }
  label {
    display: block;
    cursor: pointer;
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    padding-left: 38px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 34px;
      height: 20px;
      background: #b3bdbc;
      border-radius: 10px;
      left: 0;
      top: 50%;
      margin-top: -10px;
      transition: 0.3s ease-in-out;
    }
    &::after {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background: #fff;
      left: 2px;
      top: 50%;
      margin-top: -8px;
      transition: 0.3s ease-in-out;
    }
  }
}
