.portfolio {
  position: relative;
  @include response(1200px) {
    padding-bottom: 48px;
  }
  &__head {
    position: relative;
    background: #f0f2f2;
    padding-top: 64px;
    padding-bottom: 64px;
    @include response(1200px) {
      padding-top: 24px;
      padding-bottom: 24px;
    }
  }
  &__title {
    position: relative;
    font-size: 40px;
    font-weight: 700;
    line-height: 44px;
    text-align: center;
    color: #01231f;
    @include response(1200px) {
      font-size: 32px;
      line-height: 35px;
    }
  }
  &__body {
    position: relative;
    max-width: 1280px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 32px;
    @include response(1200px) {
      padding-top: 16px;
    }
  }
  &__filters {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 32px;
    @include response(1200px) {
      display: block;
    }
  }
  &__filters-left {
    position: relative;
    @include response(1200px) {
      margin-bottom: 8px;
    }
  }
  &__select {
    position: relative;
    display: flex;
    align-items: center;
    background: #e6e9e9;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      pointer-events: none;
      border: 1px solid #b3bdbc;
    }
    .select-parent {
      position: relative;
      min-width: 152px;
      @include response(1200px) {
        flex: 1 1 auto;
      }
    }
    .select2-container .select2-selection--single {
      height: 44px;
    }
    .select2-container .select2-selection--single .select2-selection__rendered {
      padding-left: 12px;
      padding-right: 44px;
      font-family: Public Sans;
      font-size: 14px;
      font-weight: 700;
      line-height: 44px;
      color: #80918f;
    }
    .select2-container--default .select2-selection--single {
      background: transparent;
      border: 0;
    }
    .select2-container--default .select2-selection--single .select2-selection__arrow {
      height: 44px;
      width: 44px;
    }
    .select2-container {
      width: 100%;
      left: 0 !important;
    }
    .select2-dropdown {
      width: 100% !important;
    }
    .select2-results__option {
      text-transform: uppercase;
      font-family: Public Sans;
      font-size: 12px;
      font-weight: 700;
      line-height: 16px;
      padding: 10px 0;
      border-bottom: 1px solid #b3bdbc;
      transition: 0.3s ease-in-out;
      &:last-child {
        border-bottom: 0;
      }
    }
    .select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
      background-color: transparent;
      color: #ff3d00;
    }
    .select2-results__options {
      padding: 5px 15px;
    }
    .select2-container--default .select2-results__option--selected {
      color: #ff3d00 !important;
      background-color: transparent;
    }
  }
  &__select-title {
    position: relative;
    flex: 0 0 auto;
    padding-right: 12px;
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;
    padding-left: 16px;
    text-align: left;
    color: #80918f;
    &::before {
      content: "";
      position: absolute;
      width: 1px;
      height: 28px;
      background: #b3bdbc;
      top: 50%;
      margin-top: -14px;
      right: 0;
    }
  }
  &__filters-right {
    position: relative;
  }
  &__checkboxes {
    position: relative;
    display: flex;
    @include response(1200px) {
      margin-left: -15px;
      margin-right: -15px;
      padding-left: 15px;
      padding-right: 15px;
      overflow-x: auto;
    }
  }
  &__checkbox {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    border: 1px solid #b3bdbc;
    padding: 3px 11px 3px 37px;
    margin-left: 8px;
    border-radius: 2px;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    cursor: pointer;
    @include response(1200px) {
      margin-left: 0;
      margin-right: 8px;
    }
    input {
      display: none;
      &:checked + span {
        &::before {
          background: #4d6562;
        }
      }
    }
    img {
      margin-right: 10px;
    }
    span {
      &::before {
        content: "";
        position: absolute;
        width: 16px;
        height: 16px;
        border-radius: 50px;
        border: 1px solid #4d6562;
        left: 11px;
        top: 50%;
        margin-top: -8px;
        transition: 0.3s ease-in-out;
      }
      &::after {
        content: "";
        position: absolute;
        width: 7px;
        height: 4px;
        border-bottom: 1px solid #fff;
        border-left: 1px solid #fff;
        transform: rotate(-45deg);
        left: 15px;
        top: 18px;
      }
    }
  }
  &__list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-left: -40px;
    margin-bottom: 24px;
    @include response(1200px) {
      margin-left: 0;
      display: block;
    }
  }
  &__item {
    position: relative;
    flex: 0 0 calc(50% - 40px);
    max-width: calc(50% - 40px);
    width: 100%;
    margin-left: 40px;
    margin-bottom: 40px;
    text-decoration: none;
    color: #4d6562;
    @include response(1200px) {
      display: block;
      max-width: 100%;
      margin-bottom: 16px;
      margin-left: 0;
    }
    @include desktop-hover() {
      .portfolio__item-img {
        img {
          transform: scale(1.03);
        }
      }
    }
  }
  &__item-img {
    position: relative;
    margin-bottom: 4px;
    height: 356px;
    overflow: hidden;
    border-radius: 2px;
    @include response(1200px) {
      height: 219px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.6s ease-in-out;
    }
  }
  &__item-body {
    position: relative;
    padding: 12px 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #fff;
    border: 1px solid #b3bdbc;
    border-radius: 2px;
    @include response(1200px) {
      display: block;
    }
  }
  &__item-left {
    position: relative;
    padding-right: 10px;
    @include response(1200px) {
      padding-right: 0;
    }
  }
  &__item-description {
    position: relative;
    display: flex;
    align-items: center;
  }
  &__item-description-img {
    position: relative;
    flex: 0 0 36px;
    width: 36px;
    height: 36px;
    margin-right: 4px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-description-title {
    position: relative;
    font-family: Public Sans;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    @include response(1200px) {
      font-size: 14px;
      line-height: 20px;
    }
    span {
      font-style: italic;
      font-weight: 600;
    }
  }
  &__item-name {
    position: relative;
    font-size: 19px;
    font-weight: 700;
    line-height: 29px;
    color: #01231f;
    @include response(1200px) {
      font-size: 17px;
      padding-bottom: 8px;
      border-bottom: 1px solid #b3bdbc;
    }
  }
  &__item-right {
    position: relative;
    flex: 0 0 auto;
    border-left: 1px solid #b3bdbc;
    padding-left: 24px;
    @include response(1200px) {
      padding-top: 8px;
      padding-left: 0;
      border-left: 0;
      display: flex;
      align-items: center;
    }
  }
  &__item-price-title {
    position: relative;
    font-size: 16px;
    font-weight: 400;
    line-height: 25px;
    @include response(1200px) {
      font-size: 14px;
      line-height: 22px;
      flex: 0 0 auto;
      margin-right: 4px;
    }
  }
  &__item-price-val {
    font-size: 22px;
    font-weight: 700;
    line-height: 26px;
    color: #01231f;
    @include response(1200px) {
      font-size: 19px;
      line-height: 22px;
    }
  }
  &__more-box {
    display: flex;
    justify-content: center;
    @include response(1200px) {
      padding-top: 20px;
    }
  }
  &__more-btn {
    position: relative;
    font-family: Public Sans;
    font-size: 17px;
    font-weight: 700;
    line-height: 26px;
    text-align: center;
    padding: 12px 32px;
    text-transform: uppercase;
    cursor: pointer;
    background: #80918f;
    color: #fff;
    border-radius: 2px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: #4d6562;
    }
  }
  &__mobile-form {
    display: none;
    @include response(1200px) {
      display: block;
      position: relative;
      padding: 4px 16px 10px;
    }
  }
  &__mobile-form-input {
    position: relative;
    width: 100%;
    padding: 8px 28px 8px 0;
    font-family: Public Sans;
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;
    color: #80918f;
    border: 0;
    border-bottom: 1px solid #80918f;
    transition: .3s ease-in-out;
    &:focus {
      border-bottom-color: #000;
    }
    &::placeholder {
      color: #80918f;
    }
  }
  &__mobile-form-btn {
    position: absolute;
    top: 0;
    bottom: 10px;
    right: 16px;
    width: 28px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    border: 0;
    background: transparent;
  }
}
