.popup-comment {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateX(100%);
  opacity: 0;
  visibility: hidden;
  &.active {
    opacity: 1;
    visibility: visible;
    transform: none;
  }
  &__overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(#01231f, 0.5);
    opacity: 0;
    transition: 0.3s ease-in-out;
    .popup-comment.active & {
      opacity: 1;
    }
  }
  &__box {
    position: relative;
    max-width: 672px;
    width: 100%;
    background: #fff;
    opacity: 0;
    transform: translateY(20px);
    transition: 0.3s ease-in-out;
    .popup-comment.active & {
      opacity: 1;
      transform: translateY(0);
    }
  }
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px;
    border-bottom: 1px solid #e6e9e9;
  }
  &__title {
    position: relative;
    padding-right: 10px;
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
    color: #4d6562;
  }
  &__close {
    position: relative;
    flex: 0 0 20px;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    @include desktop-hover() {
      svg {
        stroke: #ff3d00;
      }
    }
    svg {
      stroke: #94a3b8;
      transition: 0.3s ease-in-out;
    }
  }
  &__body {
    position: relative;
    padding: 24px 32px 16px;
    @include response(1200px) {
      padding: 16px;
    }
  }
  &__body-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 6px;
  }
  &__body-title {
    position: relative;
    font-size: 14px;
    font-weight: 600;
    line-height: 19px;
    color: #4d6562;
  }
  &__counter {
    position: relative;
    display: flex;
    align-items: center;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
  }
  &__counter-current {
    position: relative;
  }
  &__textarea {
    position: relative;
    background: #f0f2f2;
    width: 100%;
    height: 246px;
    resize: none;
    padding: 16px;
    border: 1px solid #e6e9e9;
    font-family: Public Sans;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    color: #01231f;
    vertical-align: top;
    &::placeholder {
      color: #01231f;
    }
  }
  &__btn-box {
    position: relative;
    padding-top: 16px;
    display: flex;
    justify-content: flex-end;
  }
  &__btn {
    position: relative;
    text-align: center;
    background: #80918f;
    padding: 8px 32px;
    cursor: pointer;
    border: none;
    font-family: Public Sans;
    font-size: 14px;
    font-weight: 700;
    line-height: 19px;
    border-radius: 2px;
    color: #fff;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: #4d6562;
    }
  }
}